@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&display=swap');
*{
  margin: 0;
  padding: 0;
  outline: 0;
  border: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}
:root{
  --color-bg: #562424;
  --color-bg-variant: #6d3636;
  --color-primary: #e28787;
  --color-primary-varaint: rgba(77,181,255,0.4);
  --color-white: #fff;
  --color-light: rgba(255,255,255,0.6);

  --conatiner-width-lg: 75%;
  --conatiner-width-md: 86%;
  --conatiner-width-sm: 90%;

  --transition: all 400ms ease;  
}

html{
  scroll-behavior: smooth;
}

::-webkit-scrollbar{
  display: none;
}

body{
  font-family: 'Montserrat', sans-serif;
  background-color: var(--color-bg);
  color: var(--color-white);
  line-height: 1.7rem;
  background-image: url("Z:\THIS\React\Portfolio\portfolio\src\assets\bg-texture.png");
}


/* ===================GENERAL STYLES==================== */

.container{
  width: var(--conatiner-width-lg);
  margin: 0 auto;
}

h1,h2,h3,h4,h5{
  font-weight: 500;
}

h1{
  font-size: 2.5rem;
}

section{
  margin-top: 4rem;
  
}

section > h2, section >h5{
  text-align: center;
  color: var(--color-light);
}
section>h2{
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light{
  color:var(--color-light);
}

a{
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover{
  color: var(--color-white); 
}

.btn{
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem; 
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover{
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary{
  background-color: var(--color-primary);
  color: var(--color-bg);
}

img{
  display: block;
  width: 100%;
  object-fit: cover;
}

/*======================= FOR MEDIUM DEVICES====================== */
@media screen and (max-width:1024px){
  .container{
    width: var(--conatiner-width-md);
  }

  section{
    margin-top: 6rem;
  }
  
}

@media screen and (max-width:600px){
  .container{
    width: var(--conatiner-width-sm);
  }

  section >h2{
    margin-bottom: 2rem;
  }
  
}