.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%
}

.about__me{
    width: 100%;
    aspect-ratio: 1/1;
    border-radius: 2rem;
    background: linear-gradient(45deg,transparent,var(--color-primary),transparent);
    display: grid;
    place-items: center;
}

.about-me__img{
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about-me__img:hover{
    transform: rotate(0deg);
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    margin-bottom: 3rem;
    gap: 2rem;
}

.about__card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__card:hover{
    transition: var(--transition);
    border-color: var(--color-primary-variant);
    cursor: default;
}

.about__icon{
    color: var(--color-primary);
    font-size: 2rem;
    margin-bottom: 1rem;
}

.description{
    margin-bottom: 2rem;
}


/*======================= FOR MEDIUM DEVICES====================== */

@media screen and (max-width:1024px){
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__content p{
        margin: 1rem auto 1.5rem;
    }
}



/*======================= FOR SMALL DEVICES====================== */
@media screen and (max-width:600px){
    .about__me{
        width: 65%;
        margin: 0 auto 3rem;
    }

    .about__cards{
        grid-template-columns: 1fr;
    }

    .about__card{
        margin: 0 3rem 0 3rem;
    }

    .about__content{
        text-align: center;
    }

    .about__content p{
        margin: 1rem auto 1.5rem;
    }
}